.banner {
  width: 100%;
  height: 267px;
  background-image: url(./img/mobile-banner.jpeg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}

.collapse:not(.show) {
  display: inherit;
}

@media only screen and (min-width: 439px) {
  .banner {
    min-height: 215px;
    background-image: url(./img/desktop-banner.jpeg);
  }
}
